import React, { Component } from 'react';
import ContactList from '../../components/ContactList'
import ContactService from '../../services/ContactService.js'
import ContactFilter from '../../components/ContactFilter/ContactFilter'
import './ContactPage.css'


class ContactPage extends Component {
    constructor (){
        super();
        this.state={
            contacts:[] 
        }
        this.query = this.query.bind(this);
    }

    componentDidMount(){
        ContactService.getContacts()
        .then(contacts => {this.setState({contacts})
        })  
    }
    query (filter){
        ContactService.getContacts(filter)
        .then(contacts=> this.setState({contacts}))
    }
    getContact(contactId){
        console.log ('nono' , contactId);
        // this.props.history.push(`/contact/${contactId}`)
        // ContactService.getContactById(contactId)
        // .then ()
    }

    render() {
        return (
            <div>
                <div> <ContactFilter onFilter={this.query}/></div>
                <div>
                    <ContactList  getContact={this.getContact} contacts={this.state.contacts}/>
                </div>
            </div>
        )
    }
}
    export default ContactPage



