import React, { Component } from 'react';

class ContactFilter extends Component {
    constructor() {
        super();
        this.state = {
            filterBy: ''
        }
        this.handleChange = this.handleChange.bind(this);
    }
    handleChange(e) {
        this.setState({
            filterBy: e.target.value,
        })
        setTimeout(e => this.props.onFilter(this.state.filterBy), 100)
    }

    render() {
        return (
            <div className="contact-filter">
                <div>filter</div>
                <form onSubmit={e => e.preventDefault()} >
                    <input onChange={this.handleChange} value={this.state.filterBy.name} type="text" name="name" />
                </form>
            </div>
        )
    }
}
export default ContactFilter



